@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap');

$family-primary: 'Jost';
$family-secondary: 'Kanit';
$family-additional: 'Roboto Condensed';
$size-title: 22px;
$size-wide: 20px;
$size-subtitle: 18px;
$size-description: 16px;
$size-small: 14px;
$size-tiny: 12px;
$weight-wide: 900;
$weight-bold: 700;
$weight-primary: 500;
$weight-thin: 300;
$weight-tiny: 100;
$color-background: var(--tg-theme-bg-color);
$color-primary: var(--tg-theme-primary-color);
$color-secondary: var(--tg-theme-secondary-color);
$color-inactive: var(--tg-theme-inactive-color);
$color-hint: var(--tg-theme-hint-color);
$color-error: var(--tg-theme-error-color);
$color-border: var(--tg-theme-border-color);
$color-button: var(--tg-theme-button-color);
$color-button-text: var(--tg-theme-button-text-color);
$color-input: var(--tg-theme-input-color);
$color-input-text: var(--tg-theme-input-text-color);
$color-icon: var(--tg-theme-icon-color);
$color-name: var(--tg-theme-body-color);
$color-title: var(--tg-theme-body-color);
$color-subtitle: var(--tg-theme-body-color);
$color-description: var(--tg-theme-body-color);
$color-link: var(--tg-theme-link-color);
$color-subtle: var(--tg-theme-subtle-color);